.singlePost {
	flex: 9;
}

.singlePostWrapper {
	padding: 20px;
	padding-right: 0;
	display: flex;
	flex-direction: column;
}

.singlePostImg {
	width: 100%;
	height: 300px;
	border-radius: 5px;
	object-fit: cover;
}

.singlePostTitle {
	text-align: center;
	margin: 10px;
	font-family: "Lora", serif;
	font-size: 28px;
	color: grey;
}

.singlePostTitleInput {
	margin: 10px;
	font-family: "Lora", serif;
	font-size: 28px;
	text-align: center;
	border: none;
	color: grey;
	border-bottom: 1px solid lightgrey;
}
.singlePostTitleInput:focus {
	outline: none;
}

.singlePostEdit {
	float: right;
	font-size: 16px;
}

.singlePostIcon {
	margin-left: 10px;
	cursor: pointer;
}

.singlePostIcon:first-child {
	color: teal;
}

.singlePostIcon:last-child {
	color: tomato;
}

.singlePostInfo {
	margin-bottom: 20px;
	display: flex;
	justify-content: space-between;
	font-size: 16px;
	color: #b39656;
	font-family: "Varela Round", sans-serif;
}

.singlePostDescription {
	color: #666;
	font-size: 18px;
	line-height: 25px;
}

.singlePostDescriptionInput {
	border: none;
	color: #666;
	font-size: 18px;
	line-height: 25px;
}
.singlePostDescriptionInput:focus {
	outline: none;
}

.singlePostDescription::first-letter {
	margin-left: 30px;
	font-size: 30px;
	font-weight: 600;
}

.singlePostButton {
	width: 100px;
	border: none;
	background-color: teal;
	padding: 5px;
	color: white;
	border-radius: 5px;
	cursor: pointer;
	align-self: flex-end;
	margin-top: 20px;
}
